/**
 * The following styles get applied inside the editor only.
 *
 */
/* overrides the :after styling of gutenberg */
.block-editor-block-list__layout .wp-block-us-info-block::after {
  top: 0;
  margin-top: -32px;
  content: "Info";
  position: absolute;
  color: #fff;
  left: 2rem;
  font-weight: bold;
}

